import React from "react";
import { useSize } from "ahooks";
import GUI from "lil-gui";
import { useEffect, useRef, useState } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

let scene: THREE.Scene,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera,
  controls: OrbitControls,
  clock: THREE.Clock,
  points_1: THREE.Points<THREE.BufferGeometry, THREE.PointsMaterial>,
  points_2: THREE.Points<THREE.BufferGeometry, THREE.PointsMaterial>,
  points_3: THREE.Points<THREE.BufferGeometry, THREE.PointsMaterial>

const Chapter6_2 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const createPoints = (url: string, size = 0.5) => {
    const particlesGeometry = new THREE.BufferGeometry();
    const count = 10000;

    // 
    const positions = new Float32Array(count * 3);
    // 
    const colors = new Float32Array(count * 3);
    // 
    for (let i = 0; i < count * 3; i++) {
      positions[i] = (Math.random() - 0.5) * 100;
      colors[i] = Math.random();
    }
    particlesGeometry.setAttribute(
      "position",
      new THREE.BufferAttribute(positions, 3)
    );
    particlesGeometry.setAttribute("color", new THREE.BufferAttribute(colors, 3));

    // 
    const pointsMaterial = new THREE.PointsMaterial();
    pointsMaterial.size = 0.5;
    pointsMaterial.color.set(0xfff000);
    // 
    pointsMaterial.sizeAttenuation = true;

    // 
    const textureLoader = new THREE.TextureLoader();
    const texture = textureLoader.load(url);
    // 
    pointsMaterial.map = texture;
    pointsMaterial.alphaMap = texture;
    pointsMaterial.transparent = true;
    pointsMaterial.depthWrite = false;
    pointsMaterial.blending = THREE.AdditiveBlending;
    // 
    pointsMaterial.vertexColors = true;

    const points = new THREE.Points(particlesGeometry, pointsMaterial);
    return points;
  }

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    points_1 = createPoints(`/textures/particles/1.png`, 1.5);
    points_2 = createPoints(`/textures/particles/1.png`, 1);
    points_3 = createPoints(`/textures/particles/xh.png`, 2);
    scene.add(points_1)
    scene.add(points_2)
    scene.add(points_3)

    /** ---  --- */
    // 
    const ambient = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambient);

    //
    const pointLight = new THREE.PointLight(0xff0000, 1);
    pointLight.castShadow = true;
    // 
    pointLight.shadow.radius = 20;
    // 
    pointLight.shadow.mapSize.set(512, 512);
    pointLight.decay = 0;

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    // 
    camera.position.set(0, 0, 10);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    // 
    renderer.shadowMap.enabled = true;
    renderer.physicallyCorrectLights = true;
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    controls = new OrbitControls(camera, renderer.domElement);

    
    clock = new THREE.Clock();
    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  function render() {
    let time = clock.getElapsedTime();
    points_1.rotation.x = time * 0.3;
    points_2.rotation.x = time * 0.5;
    points_2.rotation.y = time * 0.4;
    points_3.rotation.x = time * 0.2;
    points_3.rotation.y = time * 0.2;
    controls.update();
    renderer.render(scene, camera);
    // render
    requestAnimationFrame(render);
  }

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  return (
    <div
      id="container"
      style={{ width: "100%", height: "100%" }}
      ref={ref}
    ></div>
  );
};

export default Chapter6_2;
