import React from "react";
import { useSize } from "ahooks";
import { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

let scene: THREE.Scene,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera,
  controls: OrbitControls,
  cubeArr: THREE.Mesh<THREE.BoxGeometry, THREE.MeshBasicMaterial>[] = [],
  rayCaster: THREE.Raycaster,
  mouse: THREE.Vector2,
  redMaterial: THREE.MeshBasicMaterial,
  material: THREE.MeshBasicMaterial


const Chapter7_1 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    const boxGeometry = new THREE.BoxGeometry(1, 1, 1);
    material = new THREE.MeshBasicMaterial({ wireframe: true });
    redMaterial = new THREE.MeshBasicMaterial({ color: '#ff0000' })
    // 
    for (let i = 0; i < 5; i++) {
      for (let j = 0; j < 5; j++) {
        for (let k = 0; k < 5; k++) {
          const cube = new THREE.Mesh(boxGeometry, material)
          cube.position.set(i, j, k)
          scene.add(cube)
          cubeArr.push(cube)
        }
      }
    }

    // 
    rayCaster = new THREE.Raycaster();
    // 
    mouse = new THREE.Vector2();

    /** ---  --- */
    // 
    const ambient = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambient);

    //
    const pointLight = new THREE.PointLight(0xff0000, 1);
    pointLight.castShadow = true;
    // 
    pointLight.shadow.radius = 20;
    // 
    pointLight.shadow.mapSize.set(512, 512);
    pointLight.decay = 0;

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      300
    );
    // 
    camera.position.set(0, 0, 20);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    // 
    renderer.shadowMap.enabled = true;
    renderer.physicallyCorrectLights = true;
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    controls = new OrbitControls(camera, renderer.domElement);

    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  function render() {
    controls.update();
    renderer.render(scene, camera);
    // render
    requestAnimationFrame(render);
  }

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  const mouseClick = (event: MouseEvent) => {
    const width = Number(size?.width); //
    const height = Number(size?.height); //
    mouse.x = (event.offsetX / width) * 2 - 1;
    mouse.y = -((event.offsetY / height) * 2 - 1);
    rayCaster.setFromCamera(mouse, camera);
    let result = rayCaster.intersectObjects(cubeArr);
    result.forEach((item: any) => {
      item.object.material = redMaterial
    });
    if (result.length === 0) {
      cubeArr.forEach((item: any) => {
        item.material = material
      });
    }
  }

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
    window.addEventListener("click", mouseClick)
    return () => {
      window.removeEventListener("click", mouseClick)
    }
  }, [size?.width, size?.height]);

  return (
    <div
      id="container"
      style={{ width: "100%", height: "100%" }}
      ref={ref}
    ></div>
  );
};

export default Chapter7_1;
