import React from "react";
import { useSize } from "ahooks";
import { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import * as CANNON from "cannon-es";

let renderer: THREE.WebGLRenderer, camera: THREE.PerspectiveCamera;

const Chapter9_1 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const initThree = () => {
    /** ---  --- */
    const scene = new THREE.Scene();

    /** ---  --- */
    // 
    const ambient = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambient);
    //
    const dirLight = new THREE.DirectionalLight(0xffffff, 1);
    dirLight.castShadow = true;
    scene.add(dirLight);

    /** ---  --- */
    const sphereGeometry = new THREE.SphereGeometry(1, 20, 20);
    const sphereMaterial = new THREE.MeshStandardMaterial();
    const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
    sphere.castShadow = true;
    scene.add(sphere);

    const floor = new THREE.Mesh(
      new THREE.PlaneGeometry(20, 20),
      new THREE.MeshStandardMaterial()
    );
    floor.position.set(0, -5, 0);
    floor.rotation.x = -Math.PI / 2;
    floor.receiveShadow = true;
    scene.add(floor);

    /** ---  --- */
    const world = new CANNON.World();
    // y
    world.gravity.set(0, -9.8, 0);
    // 
    const sphereShape = new CANNON.Sphere(1);
    // 
    const sphereWorldMaterial = new CANNON.Material();
    // 
    const sphereBody = new CANNON.Body({
      shape: sphereShape,
      material: sphereWorldMaterial,
      mass: 1, // 
      position: new CANNON.Vec3(0, 0, 0), // three.js
    });
    world.addBody(sphereBody);

    // 
    const floorShape = new CANNON.Plane();
    const floorBody = new CANNON.Body();
    // 0
    floorBody.mass = 0;
    floorBody.addShape(floorShape);
    // 
    floorBody.position.set(0, -5, 0);
    // 
    floorBody.quaternion.setFromAxisAngle(
      new CANNON.Vec3(1, 0, 0),
      -Math.PI / 2
    );
    world.addBody(floorBody);

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      300
    );
    // 
    camera.position.set(0, 0, 18);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    // 
    renderer.shadowMap.enabled = true;
    renderer.physicallyCorrectLights = true;
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    const controls = new OrbitControls(camera, renderer.domElement);

    const clock = new THREE.Clock();

    function render() {
      let deltaTime = clock.getDelta();
      // 
      world.step(1 / 120, deltaTime);
      sphere.position.copy(sphereBody.position as any);

      controls.update();
      renderer.render(scene, camera);
      // render
      requestAnimationFrame(render);
    }
    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  return (
    <div
      id="container"
      style={{ width: "100%", height: "100%" }}
      ref={ref}
    ></div>
  );
};

export default Chapter9_1;
