import React from "react";
import { useSize } from "ahooks";
import GUI from "lil-gui";
import { useEffect, useRef, useState } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

let scene: THREE.Scene,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera,
  controls: OrbitControls,
  axesHelper;

const Chapter5_2 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    /** ---  --- */
    // 
    const geometry = new THREE.SphereGeometry(1, 20, 20);
    const material = new THREE.MeshStandardMaterial();
    // 
    const mesh = new THREE.Mesh(geometry, material);
    // 
    mesh.castShadow = true;
    // 
    scene.add(mesh);

    // 
    const planeGeometry = new THREE.PlaneGeometry(50, 50);
    const plane = new THREE.Mesh(planeGeometry, material);
    plane.position.set(0, -1, 0);
    plane.rotation.x = -Math.PI / 2;
    // 
    plane.receiveShadow = true;
    scene.add(plane);

    /** ---  --- */
    // 
    const ambient = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambient);
    // 
    const spotLight = new THREE.SpotLight(0xffffff, 1);
    spotLight.position.set(5, 5, 5);
    spotLight.castShadow = true;
    spotLight.intensity = 2;

    // 
    spotLight.shadow.radius = 20;
    // 
    spotLight.shadow.mapSize.set(512, 512);
    spotLight.target = mesh;
    spotLight.angle = Math.PI / 6;
    spotLight.distance = 0;
    spotLight.penumbra = 0;
    spotLight.decay = 0;

    scene.add(spotLight);

    const panel = new GUI();
    const spotLightPanel = panel.addFolder("");
    spotLightPanel.add(mesh.position, "x").min(-5).max(5).step(0.1);
    spotLightPanel
      .add(spotLight, "angle")
      .min(0)
      .max(Math.PI / 2)
      .step(0.01);
    spotLightPanel.add(spotLight, "distance").min(0).max(10).step(0.01);
    spotLightPanel.add(spotLight, "penumbra").min(0).max(1).step(0.01);
    spotLightPanel.add(spotLight, "decay").min(0).max(5).step(0.01);

    /** ------ */
    axesHelper = new THREE.AxesHelper(5);
    scene.add(axesHelper);

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    // 
    camera.position.set(0, 0, 10);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    renderer.shadowMap.enabled = true;
    renderer.physicallyCorrectLights = true;
    renderer.setClearColor(0xb9d3ff, 1); //
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    controls = new OrbitControls(camera, renderer.domElement);

    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  function render() {
    controls.update();
    renderer.render(scene, camera);
    // render
    requestAnimationFrame(render);
  }

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  return (
    <div
      id="container"
      style={{ width: "100%", height: "100%" }}
      ref={ref}
    ></div>
  );
};

export default Chapter5_2;
