import React from "react";
import { useSize } from "ahooks";
import { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import GUI from "lil-gui";
import { MeshLambertMaterial } from "three";

let scene: THREE.Scene | THREE.Object3D<THREE.Event>,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera,
  controls: OrbitControls,
  axesHelper,
  mesh: THREE.Mesh;

const Chapter2_3 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    /** ---  --- */
    // 
    const geometry = new THREE.BoxGeometry(1, 1, 1);
    // 
    const material = new THREE.MeshLambertMaterial({ color: 0x0000ff });
    // 
    mesh = new THREE.Mesh(geometry, material);
    console.log("mesh:", mesh);
    // 
    scene.add(mesh);

    /** ---  --- */
    // 
    const point = new THREE.PointLight(0xffffff);
    point.position.set(4, 2, 3);
    scene.add(point); //
    //
    const ambient = new THREE.AmbientLight(0x444444);
    scene.add(ambient);

    /** ------ */
    axesHelper = new THREE.AxesHelper(5);
    scene.add(axesHelper);

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    // 
    camera.position.set(0, 0, 5);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    renderer.setClearColor(0xb9d3ff, 1); //
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    controls = new OrbitControls(camera, renderer.domElement);

    /** ------ */
    const panel = new GUI();
    panel
      .add(mesh, "visible")
      .name("")
      .onChange(() => console.log(`${mesh.visible}`));
    panel
      .addColor(mesh.material, "color")
      .name("")
      .onChange((v: string) => {
        const material = mesh.material as MeshLambertMaterial
        material.color.set(v)
      });
    const positionPanel = panel.addFolder("");
    positionPanel
      .add(mesh.position, "x")
      .min(0)
      .max(5)
      .step(0.01)
      .name("x");
    positionPanel
      .add(mesh.position, "y")
      .min(0)
      .max(5)
      .step(0.01)
      .name("y");
    positionPanel
      .add(mesh.position, "z")
      .min(0)
      .max(5)
      .step(0.01)
      .name("z");

    /** ------ */
    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  function render() {
    controls.update();
    renderer.render(scene, camera);
    // render
    requestAnimationFrame(render);
  }

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    // 
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  return (
    <div id="container" style={{ width: "100%", height: "100%" }} ref={ref} />
  );
};

export default Chapter2_3;
