import React from "react";
import { useSize } from "ahooks";
import GUI from "lil-gui";
import { useEffect, useRef, useState } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

let scene: THREE.Scene,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera,
  controls: OrbitControls,
  axesHelper;

const Chapter6_1 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    const params = {
      widthSegments: 30,
      heightSegments: 30
    }
    /** ---  --- */
    // 
    const geometry = new THREE.SphereGeometry(3, params.widthSegments, params.heightSegments);
    const material = new THREE.PointsMaterial();
    material.size = 0.1;
    material.color.set(0xfff000);
    material.sizeAttenuation = true;
    // 
    const textureLoader = new THREE.TextureLoader();
    const texture = textureLoader.load("/textures/particles/2.png");
    // 
    material.map = texture;
    material.alphaMap = texture;
    material.transparent = true;
    material.depthWrite = false;
    material.blending = THREE.AdditiveBlending;
    // 
    const points = new THREE.Points(geometry, material);
    // 
    scene.add(points);

    const panel = new GUI();
    console.log('points', points)
    panel
      .add(params, 'widthSegments')
      .min(3)
      .max(100)
      .step(1)
      .name('')
      .onChange((value: number) => {
        points.geometry.dispose()
        points.geometry = new THREE.SphereGeometry(3, params.widthSegments, params.heightSegments)
      })
    panel
      .add(params, 'heightSegments')
      .min(2)
      .max(100)
      .step(1)
      .name('')
      .onChange((value: number) => {
        points.geometry.dispose()
        points.geometry = new THREE.SphereGeometry(3, params.widthSegments, params.heightSegments)
      })
    panel.add(material, "size")
      .min(0)
      .max(1)
      .step(0.01)
      .name('')
    panel.add(material, "sizeAttenuation")
      .name('')
      .onChange((visible: boolean) => {
        material.needsUpdate = true
      })
    panel.add(material, "transparent")
      .name('')
      .onChange((visible: boolean) => {
        material.needsUpdate = true
      })
    panel.add(material, "depthWrite")
      .name('')
      .onChange((visible: boolean) => {
        material.needsUpdate = true
      })
    panel.add(material, "blending", {
      "THREE.NoBlending": THREE.NoBlending,
      "THREE.NormalBlending": THREE.NormalBlending,
      "THREE.AdditiveBlending": THREE.AdditiveBlending,
      "THREE.SubtractiveBlending": THREE.SubtractiveBlending,
      "THREE.MultiplyBlending": THREE.MultiplyBlending,
      "THREE.CustomBlending": THREE.CustomBlending
    })
      .name('')
      .onChange((visible: boolean) => {
        material.needsUpdate = true
      })

    /** ---  --- */
    // 
    const ambient = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambient);

    //
    const pointLight = new THREE.PointLight(0xff0000, 1);
    pointLight.castShadow = true;
    // 
    pointLight.shadow.radius = 20;
    // 
    pointLight.shadow.mapSize.set(512, 512);
    pointLight.decay = 0;

    /** ------ */
    axesHelper = new THREE.AxesHelper(5);
    scene.add(axesHelper);

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    // 
    camera.position.set(0, 0, 10);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    // 
    renderer.shadowMap.enabled = true;
    renderer.physicallyCorrectLights = true;
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    controls = new OrbitControls(camera, renderer.domElement);

    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  function render() {
    controls.update();
    renderer.render(scene, camera);
    // render
    requestAnimationFrame(render);
  }

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  return (
    <div
      id="container"
      style={{ width: "100%", height: "100%" }}
      ref={ref}
    ></div>
  );
};

export default Chapter6_1;
