import { useSize } from "ahooks";
import { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import VertexShader from './shader/vertex';
import FragmentShader from './shader/fragment'

let scene: THREE.Scene | THREE.Object3D<THREE.Event>,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera

const Chapter11_2 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    // 
    const textureLoader = new THREE.TextureLoader();
    const texture = textureLoader.load("/textures/flag_paralympic.jpeg");

    /** ---  --- */
    // 
    const geometry = new THREE.PlaneGeometry(1, 1, 64, 64)
    // 
    const rawShaderMaterial = new THREE.RawShaderMaterial({
      vertexShader: VertexShader,
      fragmentShader: FragmentShader,
      side: THREE.DoubleSide,
      uniforms: {
        uTime: {
          value: 0,
        },
        uTexture: {
          value: texture,
        },
      }
    })

    // 
    const mesh = new THREE.Mesh(geometry, rawShaderMaterial);
    // 
    scene.add(mesh);

    /** ---  --- */
    // 
    const point = new THREE.PointLight(0xffffff);
    point.position.set(4, 2, 3);
    scene.add(point); //
    //
    const ambient = new THREE.AmbientLight(0x444444);
    scene.add(ambient);

    /** ------ */
    const axesHelper = new THREE.AxesHelper(5);
    scene.add(axesHelper);

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      90,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    // 
    camera.position.set(0, 0, 2);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    renderer.setClearColor(0xb9d3ff, 1); //
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    const controls = new OrbitControls(camera, renderer.domElement);

    const clock = new THREE.Clock();
    function render() {
      const elapsedTime = clock.getElapsedTime();
      rawShaderMaterial.uniforms.uTime.value = elapsedTime;
      controls.update();
      renderer.render(scene, camera);
      // render
      requestAnimationFrame(render);
    }
    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  return (
    <div id="container" style={{ width: "100%", height: "100%" }} ref={ref} />
  );
};

export default Chapter11_2;
