import { useSize } from "ahooks";
import GUI from "lil-gui";
import { useEffect, useRef, useState } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

let scene: THREE.Scene | THREE.Object3D<THREE.Event>,
  renderer: THREE.WebGLRenderer,
  camera: THREE.PerspectiveCamera,
  controls: OrbitControls,
  axesHelper;

const Chapter4_5 = () => {
  const ref = useRef<HTMLDivElement>(null);
  const size = useSize(ref);
  const [loadTip, setLoadTip] = useState<string>("");

  const initThree = () => {
    /** ---  --- */
    scene = new THREE.Scene();

    /** ---  --- */
    // 
    const geometry = new THREE.BoxGeometry(1, 1, 1, 100, 100, 100);

    const loadingManager = new THREE.LoadingManager();
    loadingManager.onStart = (url, itemsLoaded, itemsTotal) => {
      setLoadTip(
        `: ${url}.\nLoaded ${itemsLoaded} of ${itemsTotal} files.`
      );
    };
    loadingManager.onProgress = (url, itemsLoaded, itemsTotal) => {
      setLoadTip(
        `: ${url}.\nLoaded ${itemsLoaded} of ${itemsTotal} files.`
      );
    };
    loadingManager.onLoad = () => {
      setLoadTip("");
    };
    loadingManager.onError = (url) => {
      setLoadTip(` ${url}`);
    };

    const textureLoader = new THREE.TextureLoader(loadingManager);
    const ColorTexture = textureLoader.load("/textures/door/color.jpg");
    const AlphaTexture = textureLoader.load("/textures/door/alpha.jpg"); // 
    const AoTexture = textureLoader.load("/textures/door/ambientOcclusion.jpg"); // 
    const DisplaceTexture = textureLoader.load("/textures/door/height.jpg"); // 
    const RoughnessTexture = textureLoader.load("/textures/door/roughness.jpg"); // 
    const MetalnessTexture = textureLoader.load("/textures/door/metalness.jpg"); // 
    const NormalTexture = textureLoader.load("/textures/door/normal.jpg");// 

    const material = new THREE.MeshStandardMaterial({
      color: "#ffff00",
      map: ColorTexture,
      alphaMap: null,
      transparent: true,
      opacity: 1,
      aoMap: null,
      aoMapIntensity: 1,
      displacementMap: DisplaceTexture,
      displacementScale: 0.1,
      roughnessMap: null,
      roughness: 1,
      metalnessMap: null,
      metalness: 1,
      normalMap: null,
      side: THREE.DoubleSide,
    });
    // aoMapUV
    geometry.setAttribute(
      "uv2",
      new THREE.BufferAttribute(geometry.attributes.uv.array, 2)
    );
    // 
    const mesh = new THREE.Mesh(geometry, material);
    // 
    scene.add(mesh);

    /** ------ */
    const panel = new GUI();
    const basicMaterialPanel = panel.addFolder("BasicMaterial");
    basicMaterialPanel
      .add(material, "alphaMap", { hasMap: AlphaTexture, none: null })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });
    basicMaterialPanel
      .add(material, "transparent")
      .name("")
      .onChange(() => console.log(`${mesh.visible}`));
    basicMaterialPanel
      .add(material, "opacity")
      .min(0)
      .max(1)
      .step(0.01)
      .name("");
    basicMaterialPanel
      .add(material, "aoMap", { hasMap: AoTexture, none: null })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });
    basicMaterialPanel
      .add(material, "aoMapIntensity")
      .min(0)
      .max(1)
      .step(0.01)
      .name("");
    basicMaterialPanel
      .add(material, "side", {
        "THREE.FrontSide": THREE.FrontSide,
        "THREE.BackSide": THREE.BackSide,
        "THREE.DoubleSide": THREE.DoubleSide,
      })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });

    const standardMaterialPanel = panel.addFolder("StandardMaterial");
    standardMaterialPanel
      .add(material, "displacementMap", { hasMap: DisplaceTexture, none: null })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });
    standardMaterialPanel
      .add(material, "displacementScale")
      .min(0)
      .max(0.1)
      .step(0.0001)
      .name("")

    standardMaterialPanel
      .add(material, "roughnessMap", { hasMap: RoughnessTexture, none: null })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });
    standardMaterialPanel
      .add(material, "roughness")
      .min(0)
      .max(1)
      .step(0.01)
      .name("")

    standardMaterialPanel
      .add(material, "metalnessMap", { hasMap: MetalnessTexture, none: null })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });
    standardMaterialPanel
      .add(material, "metalness")
      .min(0)
      .max(1)
      .step(0.01)
      .name("")
    standardMaterialPanel
      .add(material, "normalMap", { hasMap: NormalTexture, none: null })
      .name("")
      .onChange((value: any) => {
        material.needsUpdate = true;
      });

    /** ---  --- */
    // // 
    // const point = new THREE.PointLight(0xffffff);
    // point.position.set(0, 5, 10);
    // scene.add(point); //
    // 
    const ambient = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambient);
    // 
    const directionalLight = new THREE.DirectionalLight(0xffffff, 0.5);
    directionalLight.position.set(10, 10, 10);
    scene.add(directionalLight);

    /** ------ */
    axesHelper = new THREE.AxesHelper(5);
    scene.add(axesHelper);

    /** ---  --- */
    // 
    camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    // 
    camera.position.set(0, 0, 5);
    //()
    camera.lookAt(scene.position);

    // 
    renderer = new THREE.WebGLRenderer();
    renderer.setClearColor(0xb9d3ff, 1); //
    renderer.render(scene, camera);
    ref.current?.appendChild(renderer.domElement); //bodycanvas

    /** ------ */
    controls = new OrbitControls(camera, renderer.domElement);

    render();
  };

  const changeSize = () => {
    /** ---  --- */
    const width = Number(size?.width); //
    const height = Number(size?.height); //

    // 
    camera.aspect = width / height;
    // 
    camera.updateProjectionMatrix();

    //  
    renderer.setSize(width, height);
    //  
    renderer.setPixelRatio(window.devicePixelRatio);
  };

  function render() {
    controls.update();
    renderer.render(scene, camera);
    // render
    requestAnimationFrame(render);
  }

  const fullScreen = () => {
    const fullScreenElement = document.fullscreenElement;
    if (!fullScreenElement) {
      // 
      renderer.domElement.requestFullscreen();
    } else {
      //   document
      document.exitFullscreen();
    }
  };

  useEffect(() => {
    // three
    initThree();
    window.addEventListener("dblclick", fullScreen);
    return () => {
      window.removeEventListener("dblclick", fullScreen);
    };
  }, []);

  useEffect(() => {
    if (size?.width && size?.height) {
      // 
      changeSize();
    }
  }, [size?.width, size?.height]);

  return (
    <div id="container" style={{ width: "100%", height: "100%" }} ref={ref}>
      <div className="load-tip" id="tip">
        {loadTip}
      </div>
    </div>
  );
};

export default Chapter4_5;
